vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-datasubjects"}},[_vm._v("Kategorien betroffener Personen")]),_vm._v(" "),_c('p',[_vm._v("Besucher und Nutzer des Onlineangebotes (Nachfolgend bezeichnen wir die betroffenen Personen zusammenfassend\n            auch als „Nutzer“)."),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-purpose"}},[_vm._v("Zweck der Verarbeitung")]),_vm._v(" "),_c('p',[_vm._v("- Zurverfügungstellung des Onlineangebotes, seiner Funktionen und Inhalte."),_c('br'),_vm._v("\n            - Beantwortung von Kontaktanfragen und Kommunikation mit Nutzern."),_c('br'),_vm._v("\n            - Sicherheitsmaßnahmen."),_c('br'),_vm._v("\n            - Reichweitenmessung/Marketing"),_c('br'),_vm._v(" "),_c('span',{staticClass:"tsmcom"})]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-terms"}},[_vm._v("Verwendete Begrifflichkeiten ")]),_vm._v(" "),_c('p',[_vm._v("„Personenbezogene Daten“ sind alle Informationen, die sich auf eine identifizierte oder identifizierbare\n            natürliche Person (im Folgenden „betroffene Person“) beziehen; als identifizierbar wird eine natürliche\n            Person angesehen, die direkt oder indirekt, insbesondere mittels Zuordnung zu einer Kennung wie einem Namen,\n            zu einer Kennnummer, zu Standortdaten, zu einer Online-Kennung (z.B. Cookie) oder zu einem oder mehreren\n            besonderen Merkmalen identifiziert werden kann, die Ausdruck der physischen, physiologischen, genetischen,\n            psychischen, wirtschaftlichen, kulturellen oder sozialen Identität dieser natürlichen Person sind."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            „Verarbeitung“ ist jeder mit oder ohne Hilfe automatisierter Verfahren ausgeführte Vorgang oder jede solche\n            Vorgangsreihe im Zusammenhang mit personenbezogenen Daten. Der Begriff reicht weit und umfasst praktisch\n            jeden Umgang mit Daten."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            „Pseudonymisierung“ die Verarbeitung personenbezogener Daten in einer Weise, dass die personenbezogenen\n            Daten ohne Hinzuziehung zusätzlicher Informationen nicht mehr einer spezifischen betroffenen Person\n            zugeordnet werden können, sofern diese zusätzlichen Informationen gesondert aufbewahrt werden und\n            technischen und organisatorischen Maßnahmen unterliegen, die gewährleisten, dass die personenbezogenen Daten\n            nicht einer identifizierten oder identifizierbaren natürlichen Person zugewiesen werden."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            „Profiling“ jede Art der automatisierten Verarbeitung personenbezogener Daten, die darin besteht, dass diese\n            personenbezogenen Daten verwendet werden, um bestimmte persönliche Aspekte, die sich auf eine natürliche\n            Person beziehen, zu bewerten, insbesondere um Aspekte bezüglich Arbeitsleistung, wirtschaftliche Lage,\n            Gesundheit, persönliche Vorlieben, Interessen, Zuverlässigkeit, Verhalten, Aufenthaltsort oder Ortswechsel\n            dieser natürlichen Person zu analysieren oder vorherzusagen."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Als „Verantwortlicher“ wird die natürliche oder juristische Person, Behörde, Einrichtung oder andere Stelle,\n            die allein oder gemeinsam mit anderen über die Zwecke und Mittel der Verarbeitung von personenbezogenen\n            Daten entscheidet, bezeichnet."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            „Auftragsverarbeiter“ eine natürliche oder juristische Person, Behörde, Einrichtung oder andere Stelle, die\n            personenbezogene Daten im Auftrag des Verantwortlichen verarbeitet."),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-legalbasis"}},[_vm._v("Maßgebliche Rechtsgrundlagen")]),_vm._v(" "),_c('p',[_vm._v("Nach Maßgabe des Art. 13 DSGVO teilen wir Ihnen die Rechtsgrundlagen unserer Datenverarbeitungen mit. Für\n            Nutzer aus dem Geltungsbereich der Datenschutzgrundverordnung (DSGVO), d.h. der EU und des EWG gilt, sofern\n            die Rechtsgrundlage in der Datenschutzerklärung nicht genannt wird, Folgendes: "),_c('br'),_vm._v("\n            Die Rechtsgrundlage für die Einholung von Einwilligungen ist Art. 6 Abs. 1 lit. a und Art. 7 DSGVO;"),_c('br'),_vm._v("\n            Die Rechtsgrundlage für die Verarbeitung zur Erfüllung unserer Leistungen und Durchführung vertraglicher\n            Maßnahmen sowie Beantwortung von Anfragen ist Art. 6 Abs. 1 lit. b DSGVO;"),_c('br'),_vm._v("\n            Die Rechtsgrundlage für die Verarbeitung zur Erfüllung unserer rechtlichen Verpflichtungen ist Art. 6 Abs. 1\n            lit. c DSGVO;"),_c('br'),_vm._v("\n            Für den Fall, dass lebenswichtige Interessen der betroffenen Person oder einer anderen natürlichen Person\n            eine Verarbeitung personenbezogener Daten erforderlich machen, dient Art. 6 Abs. 1 lit. d DSGVO als\n            Rechtsgrundlage."),_c('br'),_vm._v("\n            Die Rechtsgrundlage für die erforderliche Verarbeitung zur Wahrnehmung einer Aufgabe, die im öffentlichen\n            Interesse liegt oder in Ausübung öffentlicher Gewalt erfolgt, die dem Verantwortlichen übertragen wurde ist\n            Art. 6 Abs. 1 lit. e DSGVO. "),_c('br'),_vm._v("\n            Die Rechtsgrundlage für die Verarbeitung zur Wahrung unserer berechtigten Interessen ist Art. 6 Abs. 1 lit.\n            f DSGVO. "),_c('br'),_vm._v("\n            Die Verarbeitung von Daten zu anderen Zwecken als denen, zu denen sie erhoben wurden, bestimmt sich nach den\n            Vorgaben des Art 6 Abs. 4 DSGVO. "),_c('br'),_vm._v("\n            Die Verarbeitung von besonderen Kategorien von Daten (entsprechend Art. 9 Abs. 1 DSGVO) bestimmt sich nach\n            den Vorgaben des Art. 9 Abs. 2 DSGVO. "),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-securitymeasures"}},[_vm._v("Sicherheitsmaßnahmen")]),_vm._v(" "),_c('p',[_vm._v("Wir treffen nach Maßgabe der gesetzlichen Vorgabenunter Berücksichtigung des Stands der Technik, der\n            Implementierungskosten und der Art, des Umfangs, der Umstände und der Zwecke der Verarbeitung sowie der\n            unterschiedlichen Eintrittswahrscheinlichkeit und Schwere des Risikos für die Rechte und Freiheiten\n            natürlicher Personen, geeignete technische und organisatorische Maßnahmen, um ein dem Risiko angemessenes\n            Schutzniveau zu gewährleisten."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Zu den Maßnahmen gehören insbesondere die Sicherung der Vertraulichkeit, Integrität und Verfügbarkeit von\n            Daten durch Kontrolle des physischen Zugangs zu den Daten, als auch des sie betreffenden Zugriffs, der\n            Eingabe, Weitergabe, der Sicherung der Verfügbarkeit und ihrer Trennung. Des Weiteren haben wir Verfahren\n            eingerichtet, die eine Wahrnehmung von Betroffenenrechten, Löschung von Daten und Reaktion auf Gefährdung\n            der Daten gewährleisten. Ferner berücksichtigen wir den Schutz personenbezogener Daten bereits bei der\n            Entwicklung, bzw. Auswahl von Hardware, Software sowie Verfahren, entsprechend dem Prinzip des Datenschutzes\n            durch Technikgestaltung und durch datenschutzfreundliche Voreinstellungen."),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-coprocessing"}},[_vm._v("Zusammenarbeit mit Auftragsverarbeitern, gemeinsam Verantwortlichen und\n            Dritten")]),_vm._v(" "),_c('p',[_vm._v("Sofern wir im Rahmen unserer Verarbeitung Daten gegenüber anderen Personen und Unternehmen\n            (Auftragsverarbeitern, gemeinsam Verantwortlichen oder Dritten) offenbaren, sie an diese übermitteln oder\n            ihnen sonst Zugriff auf die Daten gewähren, erfolgt dies nur auf Grundlage einer gesetzlichen Erlaubnis\n            (z.B. wenn eine Übermittlung der Daten an Dritte, wie an Zahlungsdienstleister, zur Vertragserfüllung\n            erforderlich ist), Nutzer eingewilligt haben, eine rechtliche Verpflichtung dies vorsieht oder auf Grundlage\n            unserer berechtigten Interessen (z.B. beim Einsatz von Beauftragten, Webhostern, etc.). "),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sofern wir Daten anderen Unternehmen unserer Unternehmensgruppe offenbaren, übermitteln oder ihnen sonst den\n            Zugriff gewähren, erfolgt dies insbesondere zu administrativen Zwecken als berechtigtes Interesse und\n            darüberhinausgehend auf einer den gesetzlichen Vorgaben entsprechenden Grundlage. "),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-thirdparty"}},[_vm._v("Übermittlungen in Drittländer")]),_vm._v(" "),_c('p',[_vm._v("Sofern wir Daten in einem Drittland (d.h. außerhalb der Europäischen Union (EU), des Europäischen\n            Wirtschaftsraums (EWR) oder der Schweizer Eidgenossenschaft) verarbeiten oder dies im Rahmen der\n            Inanspruchnahme von Diensten Dritter oder Offenlegung, bzw. Übermittlung von Daten an andere Personen oder\n            Unternehmen geschieht, erfolgt dies nur, wenn es zur Erfüllung unserer (vor)vertraglichen Pflichten, auf\n            Grundlage Ihrer Einwilligung, aufgrund einer rechtlichen Verpflichtung oder auf Grundlage unserer\n            berechtigten Interessen geschieht. Vorbehaltlich ausdrücklicher Einwilligung oder vertraglich erforderlicher\n            Übermittlung, verarbeiten oder lassen wir die Daten nur in Drittländern mit einem anerkannten\n            Datenschutzniveau, zu denen die unter dem \"Privacy-Shield\" zertifizierten US-Verarbeiter gehören oder auf\n            Grundlage besonderer Garantien, wie z.B. vertraglicher Verpflichtung durch sogenannte Standardschutzklauseln\n            der EU-Kommission, dem Vorliegen von Zertifizierungen oder verbindlichen internen Datenschutzvorschriften\n            verarbeiten (Art. 44 bis 49 DSGVO, "),_c('a',{attrs:{"href":"https://ec.europa.eu/info/law/law-topic/data-protection/data-transfers-outside-eu_de","target":"blank"}},[_vm._v("Informationsseite der EU-Kommission")]),_vm._v(").")]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-rightssubject"}},[_vm._v("Rechte der betroffenen Personen")]),_vm._v(" "),_c('p',[_vm._v("Sie haben das Recht, eine Bestätigung darüber zu verlangen, ob betreffende Daten verarbeitet werden und auf\n            Auskunft über diese Daten sowie auf weitere Informationen und Kopie der Daten entsprechend den gesetzlichen\n            Vorgaben."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sie haben entsprechend. den gesetzlichen Vorgaben das Recht, die Vervollständigung der Sie betreffenden\n            Daten oder die Berichtigung der Sie betreffenden unrichtigen Daten zu verlangen."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sie haben nach Maßgabe der gesetzlichen Vorgaben das Recht zu verlangen, dass betreffende Daten unverzüglich\n            gelöscht werden, bzw. alternativ nach Maßgabe der gesetzlichen Vorgaben eine Einschränkung der Verarbeitung\n            der Daten zu verlangen."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sie haben das Recht zu verlangen, dass die Sie betreffenden Daten, die Sie uns bereitgestellt haben nach\n            Maßgabe der gesetzlichen Vorgaben zu erhalten und deren Übermittlung an andere Verantwortliche zu fordern.\n            "),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sie haben ferner nach Maßgabe der gesetzlichen Vorgaben das Recht, eine Beschwerde bei der zuständigen\n            Aufsichtsbehörde einzureichen."),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-revokeconsent"}},[_vm._v("Widerrufsrecht")]),_vm._v(" "),_c('p',[_vm._v("Sie haben das Recht, erteilte Einwilligungen mit Wirkung für die Zukunft zu widerrufen.")]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-object"}},[_vm._v("Widerspruchsrecht")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("Sie können der künftigen Verarbeitung der Sie betreffenden Daten nach Maßgabe der gesetzlichen\n            Vorgaben jederzeit widersprechen. Der Widerspruch kann insbesondere gegen die Verarbeitung für Zwecke der\n            Direktwerbung erfolgen.")])]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-cookies"}},[_vm._v("Cookies und Widerspruchsrecht bei Direktwerbung")]),_vm._v(" "),_c('p',[_vm._v("Als „Cookies“ werden kleine Dateien bezeichnet, die auf Rechnern der Nutzer gespeichert werden. Innerhalb der\n            Cookies können unterschiedliche Angaben gespeichert werden. Ein Cookie dient primär dazu, die Angaben zu\n            einem Nutzer (bzw. dem Gerät auf dem das Cookie gespeichert ist) während oder auch nach seinem Besuch\n            innerhalb eines Onlineangebotes zu speichern. Als temporäre Cookies, bzw. „Session-Cookies“ oder „transiente\n            Cookies“, werden Cookies bezeichnet, die gelöscht werden, nachdem ein Nutzer ein Onlineangebot verlässt und\n            seinen Browser schließt. In einem solchen Cookie kann z.B. der Inhalt eines Warenkorbs in einem Onlineshop\n            oder ein Login-Status gespeichert werden. Als „permanent“ oder „persistent“ werden Cookies bezeichnet, die\n            auch nach dem Schließen des Browsers gespeichert bleiben. So kann z.B. der Login-Status gespeichert werden,\n            wenn die Nutzer diese nach mehreren Tagen aufsuchen. Ebenso können in einem solchen Cookie die Interessen\n            der Nutzer gespeichert werden, die für Reichweitenmessung oder Marketingzwecke verwendet werden. Als\n            „Third-Party-Cookie“ werden Cookies bezeichnet, die von anderen Anbietern als dem Verantwortlichen, der das\n            Onlineangebot betreibt, angeboten werden (andernfalls, wenn es nur dessen Cookies sind spricht man von\n            „First-Party Cookies“)."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Wir können temporäre und permanente Cookies einsetzen und klären hierüber im Rahmen unserer\n            Datenschutzerklärung auf."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sofern wir die Nutzer um eine Einwilligung in den Einsatz von Cookies bitten (z.B. im Rahmen einer\n            Cookie-Einwilligung), ist die Rechtsgrundlage dieser Verarbeitung Art. 6 Abs. 1 lit. a. DSGVO. Ansonsten\n            werden die personenbezogenen Cookies der Nutzer entsprechend den nachfolgenden Erläuterungen im Rahmen\n            dieser Datenschutzerklärung auf Grundlage unserer berechtigten Interessen (d.h. Interesse an der Analyse,\n            Optimierung und wirtschaftlichem Betrieb unseres Onlineangebotes im Sinne des Art. 6 Abs. 1 lit. f. DSGVO)\n            oder sofern der Einsatz von Cookies zur Erbringung unserer vertragsbezogenen Leistungen erforderlich ist,\n            gem. Art. 6 Abs. 1 lit. b. DSGVO, bzw. sofern der Einsatz von Cookies für die Wahrnehmung einer Aufgabe, die\n            im öffentlichen Interesse liegt erforderlich ist oder in Ausübung öffentlicher Gewalt erfolgt, gem. Art. 6\n            Abs. 1 lit. e. DSGVO, verarbeitet."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Falls die Nutzer nicht möchten, dass Cookies auf ihrem Rechner gespeichert werden, werden sie gebeten die\n            entsprechende Option in den Systemeinstellungen ihres Browsers zu deaktivieren. Gespeicherte Cookies können\n            in den Systemeinstellungen des Browsers gelöscht werden. Der Ausschluss von Cookies kann zu\n            Funktionseinschränkungen dieses Onlineangebotes führen."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Ein genereller Widerspruch gegen den Einsatz der zu Zwecken des Onlinemarketing eingesetzten Cookies kann\n            bei einer Vielzahl der Dienste, vor allem im Fall des Trackings, über die US-amerikanische Seite "),_c('a',{attrs:{"href":"http://www.aboutads.info/choices/"}},[_vm._v("http://www.aboutads.info/choices/")]),_vm._v(" oder die EU-Seite "),_c('a',{attrs:{"href":"http://www.youronlinechoices.com/"}},[_vm._v("http://www.youronlinechoices.com/")]),_vm._v(" erklärt werden. Des\n            Weiteren kann die Speicherung von Cookies mittels deren Abschaltung in den Einstellungen des Browsers\n            erreicht werden. Bitte beachten Sie, dass dann gegebenenfalls nicht alle Funktionen dieses Onlineangebotes\n            genutzt werden können.")]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-erasure"}},[_vm._v("Löschung von Daten")]),_vm._v(" "),_c('p',[_vm._v("Die von uns verarbeiteten Daten werden nach Maßgabe der gesetzlichen Vorgaben gelöscht oder in ihrer\n            Verarbeitung eingeschränkt. Sofern nicht im Rahmen dieser Datenschutzerklärung ausdrücklich angegeben,\n            werden die bei uns gespeicherten Daten gelöscht, sobald sie für ihre Zweckbestimmung nicht mehr erforderlich\n            sind und der Löschung keine gesetzlichen Aufbewahrungspflichten entgegenstehen. "),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Sofern die Daten nicht gelöscht werden, weil sie für andere und gesetzlich zulässige Zwecke erforderlich\n            sind, wird deren Verarbeitung eingeschränkt. D.h. die Daten werden gesperrt und nicht für andere Zwecke\n            verarbeitet. Das gilt z.B. für Daten, die aus handels- oder steuerrechtlichen Gründen aufbewahrt werden\n            müssen.")]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-changes"}},[_vm._v("Änderungen und Aktualisierungen der Datenschutzerklärung")]),_vm._v(" "),_c('p',[_vm._v("Wir bitten Sie sich regelmäßig über den Inhalt unserer Datenschutzerklärung zu informieren. Wir passen die\n            Datenschutzerklärung an, sobald die Änderungen der von uns durchgeführten Datenverarbeitungen dies\n            erforderlich machen. Wir informieren Sie, sobald durch die Änderungen eine Mitwirkungshandlung Ihrerseits\n            (z.B. Einwilligung) oder eine sonstige individuelle Benachrichtigung erforderlich wird.")]),_vm._v(" "),_c('p'),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-hostingprovider"}},[_vm._v("Hosting und E-Mail-Versand")]),_vm._v(" "),_c('p'),_vm._v(" "),_c('p',[_c('span',{staticClass:"ts-muster-content"},[_vm._v("Die von uns in Anspruch genommenen Hosting-Leistungen dienen der Zurverfügungstellung der folgenden Leistungen: Infrastruktur- und Plattformdienstleistungen, Rechenkapazität, Speicherplatz und Datenbankdienste, E-Mail-Versand, Sicherheitsleistungen sowie technische Wartungsleistungen, die wir zum Zwecke des Betriebs dieses Onlineangebotes einsetzen. "),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nHierbei verarbeiten wir, bzw. unser Hostinganbieter Bestandsdaten, Kontaktdaten, Inhaltsdaten, Vertragsdaten, Nutzungsdaten, Meta- und Kommunikationsdaten von Kunden, Interessenten und Besuchern dieses Onlineangebotes auf Grundlage unserer berechtigten Interessen an einer effizienten und sicheren Zurverfügungstellung dieses Onlineangebotes gem. Art. 6 Abs. 1 lit. f DSGVO i.V.m. Art. 28 DSGVO (Abschluss Auftragsverarbeitungsvertrag).")])]),_vm._v(" "),_c('p'),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-logfiles"}},[_vm._v("Erhebung von Zugriffsdaten und Logfiles")]),_vm._v(" "),_c('p'),_vm._v(" "),_c('p',[_c('span',{staticClass:"ts-muster-content"},[_vm._v("Wir, bzw. unser Hostinganbieter, erhebt auf Grundlage unserer berechtigten Interessen im Sinne des Art. 6 Abs. 1 lit. f. DSGVO Daten über jeden Zugriff auf den Server, auf dem sich dieser Dienst befindet (sogenannte Serverlogfiles). Zu den Zugriffsdaten gehören Name der abgerufenen Webseite, Datei, Datum und Uhrzeit des Abrufs, übertragene Datenmenge, Meldung über erfolgreichen Abruf, Browsertyp nebst Version, das Betriebssystem des Nutzers, Referrer URL (die zuvor besuchte Seite), IP-Adresse und der anfragende Provider."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nLogfile-Informationen werden aus Sicherheitsgründen (z.B. zur Aufklärung von Missbrauchs- oder Betrugshandlungen) für die Dauer von maximal 7 Tagen gespeichert und danach gelöscht. Daten, deren weitere Aufbewahrung zu Beweiszwecken erforderlich ist, sind bis zur endgültigen Klärung des jeweiligen Vorfalls von der Löschung ausgenommen.")])]),_vm._v(" "),_c('p'),_vm._v(" "),_c('h3',{attrs:{"id":"d