s sowie andere Google-Marketing-Dienste in unser Onlineangebot einbinden). Der Tag Manager selbst (welches die Tags implementiert) verarbeitet keine personenbezogenen Daten der Nutzer. Im Hinblick auf die Verarbeitung der personenbezogenen Daten der Nutzer wird auf die folgenden Angaben zu den Google-Diensten verwiesen. Nutzungsrichtlinien: "),_c('a',{attrs:{"target":"_blank","href":"https://www.google.com/intl/de/tagmanager/use-policy.html"}},[_vm._v("https://www.google.com/intl/de/tagmanager/use-policy.html")]),_vm._v("."),_c('br')])]),_vm._v(" "),_c('p'),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-ga-googleanalytics"}},[_vm._v("Google Analytics")]),_vm._v(" "),_c('p'),_vm._v(" "),_c('p',[_c('span',{staticClass:"ts-muster-content"},[_vm._v("Wir setzen Google Analytics, einen Webanalysedienst der Google Ireland Limited, Gordon House, Barrow Street, Dublin 4, Irland („Google“) ein. Google verwendet Cookies. Die durch das Cookie erzeugten Informationen über Benutzung des Onlineangebotes durch die Nutzer werden in der Regel an einen Server von Google in den USA übertragen und dort gespeichert."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nGoogle wird diese Informationen in unserem Auftrag benutzen, um die Nutzung unseres Onlineangebotes durch die Nutzer auszuwerten, um Reports über die Aktivitäten innerhalb dieses Onlineangebotes zusammenzustellen und um weitere, mit der Nutzung dieses Onlineangebotes und der Internetnutzung verbundene Dienstleistungen, uns gegenüber zu erbringen. Dabei können aus den verarbeiteten Daten pseudonyme Nutzungsprofile der Nutzer erstellt werden."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nWir setzen Google Analytics nur mit aktivierter IP-Anonymisierung ein. Das bedeutet, die IP-Adresse der Nutzer wird von Google innerhalb von Mitgliedstaaten der Europäischen Union oder in anderen Vertragsstaaten des Abkommens über den Europäischen Wirtschaftsraum gekürzt. Nur in Ausnahmefällen wird die volle IP-Adresse an einen Server von Google in den USA übertrage