rivacy")]),_vm._v(") sowie in den Einstellungen für die Darstellung von Werbeeinblendungen durch Google "),_c('a',{attrs:{"target":"_blank","href":"https://adssettings.google.com/authenticated"}},[_vm._v("(https://adssettings.google.com/authenticated")]),_vm._v(")."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nDie personenbezogenen Daten der Nutzer werden nach 14 Monaten gelöscht oder anonymisiert.")])]),_vm._v(" "),_c('p'),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-socialmedia"}},[_vm._v("Onlinepräsenzen in sozialen Medien")]),_vm._v(" "),_c('p'),_vm._v(" "),_c('p',[_c('span',{staticClass:"ts-muster-content"},[_vm._v("Wir unterhalten Onlinepräsenzen innerhalb sozialer Netzwerke und Plattformen, um mit den dort aktiven Kunden, Interessenten und Nutzern kommunizieren und sie dort über unsere Leistungen informieren zu können."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nWir weisen darauf hin, dass dabei Daten der Nutzer außerhalb des Raumes der Europäischen Union verarbeitet werden können. Hierdurch können sich für die Nutzer Risiken ergeben, weil so z.B. die Durchsetzung der Rechte der Nutzer erschwert werden könnte. Im Hinblick auf US-Anbieter die unter dem Privacy-Shield zertifiziert sind, weisen wir darauf hin, dass sie sich damit verpflichten, die Datenschutzstandards der EU einzuhalten."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\nFerner werden die Daten der Nutzer im Regelfall für Marktforschungs- und Werbezwecke verarbeitet. So können z.B. aus dem Nutzungsverhalten und sich daraus ergebenden Interessen der Nutzer Nutzungsprofile erstellt werden. Die Nutzungsprofile können wiederum verwendet werden, um z.B. Werbeanzeigen innerhalb und außerhalb der Plattformen zu schalten, die mutmaßlich den Interessen der Nutzer entsprechen. Zu diesen Zwecken werden im Regelfall Cookies auf den Rechnern der Nutzer gespeichert, in denen das Nutzungsverhalten und die Interessen der Nutzer gespeichert werden. Ferner können in den Nutzungsprofilen auch Daten unabhängig der von den Nutzern verwen