import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Privacy from '@/pages/Privacy'
import Imprint from '@/pages/Imprint'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Startseite',
      component: Home
    },
    {
      path: '/privacy',
      name: 'Datenschutz',
      component: Privacy
    },
    {
      path: '/imprint',
      name: 'Impressum',
      component: Imprint
    }
  ]
})



